@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

body {
  font-family: "Noto Sans JP", sans-serif;
}

.l-content h1 {
  font-size: var(--font-xl);
}
.l-content h2 {
  font-size: var(--font-xl);
}
.l-content h3 {
  font-size: var(--font-lg);
}
.l-content h4 {
  font-size: var(--font-md);
}
.l-content h5 {
  font-size: var(--font-sm);
}

ol,
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  box-sizing: border-box;
  vertical-align: bottom;
}

/* Breakpoints(Arkhe) */
/* 見出しデザイン */
/* トップページ */
.l-main__body.p-front > .c-postContent {
  margin-top: 0;
}

[data-sidebar=on] .l-content__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column-reverse;
  row-gap: 40px;
  padding: 0 var(--ark-padding--container) var(--ark-padding--container);
}
@media (min-width: 1000px) {
  [data-sidebar=on] .l-content__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: row-reverse;
    column-gap: var(--ark-gap--container);
  }
}

[data-sidebar=on] .l-sidebar {
  display: none;
}
@media (min-width: 1000px) {
  [data-sidebar=on] .l-sidebar {
    display: block;
    padding-bottom: 5rem;
  }
}

.p-hero__particles + .l-content__body {
  margin-top: 0;
}
@media (min-width: 600px) {
  .p-hero__particles + .l-content__body {
    margin-top: -120px;
  }
}
@media (min-width: 1000px) {
  .p-hero__particles + .l-content__body {
    margin-top: -120px;
  }
}
@media (min-width: 600px) {
  .p-hero__particles + .l-content__body .l-main {
    padding-top: 40px;
    padding-right: 40px;
    padding-left: 40px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: #ffffff;
  }
}
.p-hero__particles + .l-content__body .p-entry__head .c-postMetas {
  color: var(--color-spindle);
}

.p-hero__blendParticles + .l-content > .l-content__body {
  margin-top: 0;
}
@media (min-width: 600px) {
  .p-hero__blendParticles + .l-content > .l-content__body {
    margin-top: -120px;
    padding-top: 80px;
    padding-right: 40px;
    padding-left: 40px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: var(--color-primary);
  }
}
@media (min-width: 1000px) {
  .p-hero__blendParticles + .l-content > .l-content__body {
    margin-top: -120px;
  }
}
.p-hero__blendParticles + .l-content > .l-content__body .p-entry__head .c-postMetas {
  color: var(--color-spindle);
}

.p-postList.-type-simple .p-postList__link:hover {
  background-color: rgba(247, 247, 247, 0.4);
}

.p-fixBtnWrap:has(.c-fixBtn) {
  bottom: 8%;
}

.c-fixBtn {
  background: var(--color-linear-pewter-blue);
  border-radius: 100px;
}

.wp-block-image:not(.is-style-rounded) > a,
.wp-block-image:not(.is-style-rounded) img {
  border-radius: inherit;
  max-width: 100%;
  width: 100%;
}

.p-postList.-type-list .p-postList__item {
  padding: 0.875rem 0;
  border-bottom: 1px solid var(--ark-color--border);
}
.p-postList.-type-list .p-postList__item:first-child {
  border-top: 1px solid var(--ark-color--border);
}

.ark-block-heading .ark-block-heading__line:before {
  background-color: currentColor;
  border-radius: 2px;
  content: "";
  display: block;
  height: 2px;
  width: 2em;
}

/* メインビジュアル #top_title_area */
#top_title_area.p-topArea {
  height: 50vh;
  text-align: left;
}
#top_title_area.p-topArea .p-topArea__title {
  text-align: left;
}

.p-topArea__excerpt {
  text-align: left;
}

/**
* トップページ
**/
.p-piece .p-postList.-type-card .p-postList__item {
  margin-bottom: 1rem;
}

.p-index__news .c-more__arrow.u-uppercase {
  text-align: right;
}

/* 箇条書きリスト(チェックリスト) */
.p-post-definition__desc [class*=is-style-ark-list-icon] > li:before {
  background-color: var(--color-half-baked);
}

.l-sidebar > #ez-toc-container {
  width: 100%;
  border: 0 none;
  box-shadow: none;
}

:root,
:before,
:after {
  /*デフォルトの--ark-mtのサイズ拡張 */
  --ark-mt--zero: 0;
  --ark-mt--l: 3.75rem;
  /*arkhe-width */
  --ark-width--container--xl: 1200px;
  --ark-width--container--md: 1000px;
  --ark-width--article--md: 1000px;
  --ark-width--article--sm: 800px;
  /*arkhe-margin */
  --space-unit: 1rem;
  --space-xxs: calc(0.25 * var(--space-unit));
  --space-xs: calc(0.5 * var(--space-unit));
  --space-sm: calc(0.75 * var(--space-unit));
  --space-md: calc(1.5 * var(--space-unit));
  --space-lg: calc(2 * var(--space-unit));
  --space-xl: calc(2.5 * var(--space-unit));
  --space-xxl: calc(4 * var(--space-unit));
  --space-xxxl: calc(5 * var(--space-unit));
  --width-unit: 1rem;
  --width-xxxs: calc(0.125 * var(--width-unit));
  --arc-circle--md: 160px;
  --arc-circle--lg: 200px;
  --pseudo-arc-line: 40px;
  /* font-size */
  /*Break point: Minimum viewport width 375px to Maximum viewport width 1200px */
  --font-xxs: clamp(0.625rem, 0.5682rem + 0.2424vw, 0.75rem);
  --font-xs: clamp(0.75rem, 0.6932rem + 0.2424vw, 0.875rem);
  --font-sm: clamp(0.875rem, 0.8182rem + 0.2424vw, 1rem);
  --font-md: clamp(1rem, 0.8864rem + 0.4848vw, 1.25rem);
  --font-lg: clamp(1.25rem, 1.1364rem + 0.4848vw, 1.5rem);
  --font-xl: clamp(1.5rem, 1.3864rem + 0.4848vw, 1.75rem);
  --font-xxl: clamp(2rem, 1.7727rem + 0.9697vw, 2.5rem);
  --font-xxxl: clamp(2.5rem, 1.8182rem + 2.9091vw, 4rem);
  --font-hero: clamp(1.75rem, 1.4091rem + 1.4545vw, 2.5rem);
  --font-hero--md: clamp(36px, 5vw, 48px);
  --font-hero--lg: clamp(2.5rem, 1.5909rem + 3.8788vw, 4.5rem);
  --font-hero--xl: clamp(4rem, 3.0909rem + 3.8788vw, 6rem);
  /*  Global Font */
  --global-font-xl: clamp(
    1.875rem,
    1.2083rem + 1.3889vw,
    2.25rem
  );
  --global-font-lg: clamp(
    1.75rem,
    1.9722rem + -0.463vw,
    1.625rem
  );
  --global-font-regular: clamp(
    0.875rem,
    0.6528rem + 0.463vw,
    1rem
  );
  --color-primary: #ffffff;
  --color-secondary: #333333;
  --color-success: #062d6a;
  --color-white-smoke: #f3f3f3;
  --color-light-gray: #cccccc;
  --color-taupe-gray: #888888;
  --color-philippine-gray: #8f8f8f;
  --color-philippine-silver: #b7b7b8;
  --color-granite-gray: #5f5f5f;
  --color-lime-green: #c8e4cc;
  --color-summer-sky: #48c6ef;
  --color-pattern-blue: #dcf0fa;
  --color-bahama-blue: #1e5285;
  --color-lapis-lazuli: #26619c;
  --color-sample-blue: #2b6ba8;
  --color-picton: #5ea0e0;
  --color-spindle: #b6bdc7;
  --color-smalt: #0f238c;
  --color-indigo: #2c2261;
  --color-deep-koamaru: #2e276e;
  --color-columbia-blue: #d2d8eb;
  --color-viking: #41afcd;
  --color-golden-yellow: #ffe600;
  --color-tangerine-yellow: #ffcd00;
  --color-tarragon-tease: #b4a983;
  --color-nordic: #093637;
  --color-half-baked: #5b8f8f;
  --color-desaturated-cyan: #64969c;
  --color-pewter-blue: #89a4b3;
  --color-weldon-blue: #80999c;
  --color-light-slate-gray: #758c8f;
  --color-cadet: #5a6b6d;
  --color-cinereous: #9c8380;
  --color-antiflash: #f2f5f5;
  --color-gainsboro: #dedede;
  --color-cadet-gray: #8aa5a8;
  --color-ube: #8a87cd;
  --color-deep-blue: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
  --color-azur-lane: linear-gradient(120deg, #7f7fd5, #86a8e7);
  --color-text-primary: #333333;
  --color-text-vulcan: #3c3e42;
  --color-text-eclipse: #3c3c3c;
  --color-primary-translucent: rgba(255 255 255 / 40%);
  --color-charcoal-translucent: rgba(68 68 68 /50%);
  --color-spindle-translucent: rgba(182, 189, 199, 40%);
  --color-moonlit-asteroid: linear-gradient(
    45deg,
    rgba(15, 32, 39, 0.86),
    rgba(44, 83, 100, 0.86) 100%,
