    z-index: calc(var(--count, 10) - var(--index, 0));
    position: relative;
    grid-template-rows: unset;
  }
}
.p-index__item--img {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  height: var(--info-size);
  animation: in-n-out both linear;
  animation-timeline: --article;
}
@media (min-width: 600px) {
  .p-index__item--img {
    position: fixed;
    top: 0;
    right: 0;
    left: 40%;
    bottom: 0;
