    position: relative;
  }
}
.p-timeline::before {
  content: "";
  display: none;
  top: 8px;
  left: 8px;
  width: 2px;
  height: 100%;
  background-color: #333;
  transform: translateY(0);
  transform: translate(-50%, 0);
  z-index: -1;
}
@media (min-width: 768px) {
  .p-timeline::before {
    display: block;
    position: absolute;
  }
}
@media (min-width: 768px) {
