    column-gap: 0;
  }
}

/* ================================
  Timeline Item
================================ */
.p-timeline__item {
  width: 100%;
  padding-left: 0;
  text-align: left;
}
@media (min-width: 768px) {
  .p-timeline__item {
    position: relative;
    padding-left: 40px;
  }
}
@media (min-width: 1000px) {
  .p-timeline__item {
    padding-left: 60px;
  }
}
.p-timeline__item::before {
  display: none;
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background: #333;
  top: 0;
  border-radius: 20px;
  left: 0;
  transform: translate(15%, 45%);
}
@media (min-width: 768px) {
  .p-timeline__item::before {
    display: block;
  }
}

/* ================================
  Timeline Item Content
================================ */
.p-timeline__content {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}
.p-timeline__content.is-show {
