/* 共通モジュール */
.-textright {
  text-align: right;
}

.u-uppercase {
  text-transform: uppercase;
}

.u-capitalize {
  text-transform: capitalize;
}

.u-montserrat {
  font-family: "Montserrat", "Noto Sans JP", sans-serif;
}

.u-hero-title--main {
  font-size: var(--font-hero--md);
  font-family: "Montserrat", "Noto Sans JP", sans-serif;
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
}

.u-media--rounded {
  border-radius: 10px;
}

.u-media--drop-shadow {
  filter: drop-shadow(2px 4px 4px rgba(0, 0, 0, 0.4));
}

.u-media--pseudo {
  position: relative;
}
.u-media--pseudo::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.5);
  mix-blend-mode: color-dodge;
  top: 0;
  left: 0;
  border-radius: 10px;
  opacity: 1;
}

.js-target {
  transition: 1.4s ease-out;
  transform: translateY(4rem);
  opacity: 0;
}
.js-target.on {
  transform: translateY(0);
  opacity: 1;
}

.js-target__image:before {
  transition: 1.4s ease-out;
}
.js-target__image.on .js-target__image.on::before {
  opacity: 0.4;
}

.fadeout {
  animation: fadeOut 1s;
  animation-fill-mode: both;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.l-wrapper {
  opacity: 0; /*はじめは透過0に*/
}

/*bodyにpageOnクラスがついたらcontainerが出現*/
body.pageOn .l-wrapper {
  animation-name: PageAnimeOn;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes PageAnimeOn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
/* START SPINNER */
.p-spinner__wrapper {
  width: 100vw;
  height: 100vh;
  background: var(--color-antiflash);
  position: fixed;
  top: 0;
  left: 0;
  display: grid;
  place-items: center;
  z-index: 99999;
  opacity: 1;
  visibility: visible;
  transition-property: opacity, visibility;
  transition-duration: 3s;
}
.p-spinner__wrapper.loaded {
  opacity: 0;
  visibility: hidden;
}

.p-spinner,
.p-spinner::after {
  border-radius: 50%;
  width: 4em;
  height: 4em;
}

.p-spinner {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
}

.p-spinner::after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50%;
  border-top: 0.4em solid rgba(255, 255, 255, 0.2);
  border-right: 0.4em solid rgba(255, 255, 255, 0.2);
  border-bottom: 0.4em solid rgba(255, 255, 255, 0.2);
  border-left: 0.4em solid #545454;
  transform: translateZ(0);
  animation: spinnerAnime 1200ms infinite linear;
}

@keyframes spinnerAnime {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.p-loader {
  width: 100vw;
  height: 100vh;
  background: var(--color-antiflash);
  position: fixed;
  display: grid;
  place-items: center;
  top: 0;
  left: 0;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
}
.p-loader.loaded {
  animation: loaderAnime 2000ms ease-out;
}
@keyframes loaderAnime {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  50% {
    opacity: 1;
    visibility: visible;
  }
  80% {
    opacity: 0;
    visibility: hidden;
  }
}
.p-loader__container .p-loader__text {
  width: fit-content;
  font-size: var(--font-xxl);
  color: #333;
}

.p-curtain {
  display: block;
  content: "";
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  transform: scaleX(0);
  background: var(--color-linear-deep-koamaru);
}
.p-curtain.loaded {
  animation-name: curtainAnime;
  animation-duration: 800ms;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}
.p-curtain.change {
  will-change: transform;
}

@keyframes curtainAnime {
  0% {
    transform-origin: left;
    transform: scaleX(0);
    opacity: 0;
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
    opacity: 1;
  }
  50.001% {
    transform-origin: right;
    transform: scaleX(1);
    opacity: 1;
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
    opacity: 0;
  }
}